import { useEffect } from "react";
import { studioURL } from "../../../actions/api";
import { APPEND_CHILD } from "../../../utils";
const Studio = ({ wallID }) => {
    useEffect(() => {
        window.wall_Id = wallID;
        fetch(studioURL)
            .then(response => response.json())
            .then(({ id, script, css }) => {
                const createElement = (type, attributes) => {
                    const element = document.createElement(type);
                    Object.keys(attributes).forEach(attr => element.setAttribute(attr, attributes[attr]));
                    return element;
                };
                document.querySelector(".studio-wall").appendChild(createElement("div", { id }));
                APPEND_CHILD(css,'link')
                APPEND_CHILD(script,'script')
            });
    }, [wallID]);
    return <div className="studio-wall"></div>;
};

export default Studio;